body {


    background-color: rgb(30, 20, 32);
    background-image: -webkit-linear-gradient(45deg, rgb(30, 20, 32) 20%, rgb(16, 140, 182) 75%);
    min-height: 95vh;
    background-repeat: no-repeat;
}

img.img-fluid {
    height: 300px;
    border-style: solid;
    border-width: 5px;
    border-color: rgb(102, 150, 185);
}
h3 {
    font-size: .75rem;
}
div#root {
    margin-top: 1%;
}
#released {
    margin-top: 15px;
}
#runtime {
    font-size: .75rem;
}
#plot {
    font-size: 1rem;
}

div.card.text-center {
    border-radius: .15em;
    padding: 1em;
    box-shadow: 0 0 0 .3em rgb(102, 150, 185);
    outline: .15em solid rgb(102, 150, 185);
    background-color: rgb(246, 246, 242);
    margin-bottom: 20px;
}

div.card-header {
    border-radius: .15em;
    padding: 1em;
    box-shadow: 0 0 0 .3em rgb(174, 64, 89);
    outline: .15em solid rgb(174, 64, 89);
}

input#search.form-control {
    border-radius: .15em;
    padding: 1em;
    box-shadow: 0 0 0 .3em rgb(102, 150, 185);
    outline: .15em solid rgb(102, 150, 185);
}

button.btn.btn-primary {
    color: #fff;
    background-color:rgb(174, 64, 89);
    border-color: rgb(30, 20, 32);
}
